export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
      html
      frontmatter {
        date
        title
        tags
        main_text
        main_text1
        mmmmLinks{
          id
          link
        }
        features{
          id
          justifyCont
          background
          features{
            id
            feature
          }
          featureHeader
          feature
          featureHeadCol
          featureCol
          borderCol
          x
          y
          videoPreview
          videoPath
        }
        ffddg
        getST
        getSTSesc
        download
        docs
        docsDesc
        resourss
        mainColor
        resourcesArr{
        id
        resourceName
        resourceLink
        haveList
        resDescList{
          id
          desc
        }
        }
        downImg{
          childImageSharp {            
            fluid(              
              quality: 10 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        ResImg {
          childImageSharp {            
            fluid(              
              quality: 10 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        DocsImg {
          childImageSharp {            
            fluid(              
              quality: 10 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }

        main_image {
          childImageSharp {
            
            fluid(              
              quality: 90 
              maxWidth: 500                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        cover {
          childImageSharp {
            fluid(              
              quality: 2     
              maxWidth: 400         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        youtube_link {
          childImageSharp {
            fluid(              
              quality: 10     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        itch_link {
          childImageSharp {
            fluid(              
              quality: 10     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        unitystore_link {
          childImageSharp {
            fluid(              
              quality: 10     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        docs_link {
          childImageSharp {
            fluid(              
              quality: 10     
              maxWidth: 200         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }

      }
    }
  }
`;